// src/app/auth/signin/page.tsx – Login form (client-side for interactivity; base Supabase client for browser mutations – best for Next.js 15+ pivot, async-safe without deprecated helpers)
'use client';

import { createClient } from '@supabase/supabase-js';  // Base package client (no helpers – lighter, future-proof; uses NEXT_PUBLIC vars for browser)
import { useRouter } from 'next/navigation';
import { useState } from 'react';
import { Button } from '@/components/ui/button';  // Shadcn (consistent UI)
import { Input } from '@/components/ui/input';  // Shadcn (polished inputs)
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';  // Shadcn (groups form – "portal" frame)

export default function SignIn() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState<string | null>(null);
  const router = useRouter();

  const supabase = createClient(
    process.env.NEXT_PUBLIC_SUPABASE_URL!,
    process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!
  );

  const handleSignIn = async (e: React.FormEvent) => {
    e.preventDefault();
    if (password.length < 6) return setError('Password must be at least 6 characters');  // Logic: Validation (security best practice)
    const { error } = await supabase.auth.signInWithPassword({ email, password });
    if (error) setError(error.message);
    else router.push('/dashboard');
  };

  return (
    <div className="flex min-h-screen items-center justify-center bg-[url('/hq-background.jpg')] bg-cover bg-center text-gold-400 font-fantasy animate-fade-in">  // Logic: Background image (pixel art HQ entrance – install Next Image for opt; push back: Add via assets folder; animate-fade-in Tailwind for "portal" entry feel)
      <Card className="w-[400px] bg-dark-wood/80 border-gold-500 shadow-[0_0_20px_rgba(255,215,0,0.5)] animate-pulse">  // Logic: Themed card (ornate "portal" – gold/dark wood for MMORPG feel; pulse animation for glow)
        <CardHeader>
          <CardTitle className="text-center text-3xl">Enter Wraelen Realm</CardTitle>  // Logic: Fantasy title (sets "game" tone – push back: Add logo/image above for immersion)
        </CardHeader>
        <CardContent>
          {error && <p className="text-red-500 mb-4 text-center">{error}</p>}
          <Input
            type="email"
            placeholder="Your Email Scroll"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            className="mb-4 bg-dark-wood border-gold-500 text-gold-400 placeholder-gold-600 focus:border-gold-300"
          />
          <Input
            type="password"
            placeholder="Secret Rune"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="mb-6 bg-dark-wood border-gold-500 text-gold-400 placeholder-gold-600 focus:border-gold-300"
          />
          <Button type="submit" className="w-full bg-gold-500 text-dark-wood hover:bg-gold-600">Embark</Button>  // Logic: Themed button (fantasy language – "Embark" for game feel)
        </CardContent>
      </Card>
    </div>
  );
}